import * as THREE from 'three'
import React, { useState, useEffect, useRef } from 'react'
import { apply, Canvas, useRender } from 'react-three-fiber'
import * as meshline from 'three.meshline'
import html2canvas from "html2canvas";

apply(meshline)

function Fatline() {
  const material = useRef();  
  const [colors, setColors] = useState(['#A2CCB6', '#FCEEB5', '#EE786E', '#EE786E']);
  const [pickedColorIndex] = useState(() => parseInt(colors.length * Math.random()));
  const pickedColor = colors[pickedColorIndex];
  const [ratio] = useState(() => 0.5 + 0.5 * Math.random())
  const [width] = useState(() => Math.max(0.1, 0.3 * Math.random()))
  // Calculate wiggly curve
  const [curve] = useState(() => {
    let pos = new THREE.Vector3(30 - 60 * Math.random(), -5, 10 - 20 * Math.random())
    return new Array(30).fill().map(() => pos.add(new THREE.Vector3(2 - Math.random() * 4, 4 - Math.random() * 2, 5 - Math.random() * 10)).clone())
  })
  useEffect(() => {
    const setColorsFromGlobal = function(event) {
      if (event.data.type !== "render") {
        return;
      }
      setColors(event.data.payload.palette);
    };
    window.addEventListener('message', setColorsFromGlobal);
    return() => window.removeEventListener('message', setColorsFromGlobal);
  }, []);
  // Hook into the render loop and decrease the materials dash-offset
  useRender(() => (material.current.uniforms.dashOffset.value -= 0.0005))
  return (
    <mesh>
      {/** MeshLine and CMRCurve are a OOP factories, not scene objects, hence all the imperative code in here :-( */}
      <meshLine onUpdate={self => (self.parent.geometry = self.geometry)}>
        <geometry onUpdate={self => self.parent.setGeometry(self)}>
          <catmullRomCurve3 args={[curve]} onUpdate={self => (self.parent.vertices = self.getPoints(500))} />
        </geometry>
      </meshLine>
      {/** MeshLineMaterial on the other hand is a regular material, so we can just attach it */}
      <meshLineMaterial attach="material" ref={material} transparent depthTest={false} lineWidth={width} color={pickedColor} dashArray={0.1} dashRatio={ratio} />
    </mesh>
  )
}

function Scene() {
  const [numLines, setNumLines] = useState(100);
  let group = useRef()
  let theta = 0
  useEffect(() => {
    const setParamFromGlobal = function(event) {
      if (event.data.type !== "render") {
        return;
      }
      const {payload} = event.data;
      if (numLines !== payload.numLines)  {
        setNumLines(payload.numLines);
      }      
    };
    window.addEventListener('message', setParamFromGlobal);
    return() => window.removeEventListener('message', setParamFromGlobal);
  }, [numLines]);
  // Hook into the render loop and rotate the scene a bit
  useRender(() => group.current.rotation.set(0, 5 * Math.sin(THREE.Math.degToRad((theta += 0.02))), 0))  
  const lines = new Array(numLines).fill()
  return (
    <group ref={group}>
      {lines.map((_, index) => (
        <Fatline key={index} />
      ))}
    </group>
  )
}

function App() {
  const [backgroundColor, setBackgroundColor] = useState();
  const [invalidateFrameloop, setInvalidateFrameloop] = useState(false);
  const [fov, setFov] = useState(75);
  useEffect(() => {
    const setColorsFromGlobal = function(event) {
      if (event.data.type === "pause") {
        setInvalidateFrameloop(true);
        return;
      }
      if (event.data.type === "unpause") {
        setInvalidateFrameloop(false);
        return;
      }
      if (event.data.type === "download") {
        html2canvas(this.captureFrameRef.current, { scale: 2 }).then(canvas => {
          var image = canvas.toDataURL("image/png");
          window.parent.postMessage({ type: "download", image }, "*");
        });
        return;
      }
      if (event.data.type !== "render") {
        return;
      }
      const {payload} = event.data;
      if (backgroundColor !== payload.backgroundColor)  {
        setBackgroundColor(payload.backgroundColor);
      }      
      if (fov !== payload.fov) {
        setFov(payload.fov);
      }
    };
    window.addEventListener('message', setColorsFromGlobal);
    return() => window.removeEventListener('message', setColorsFromGlobal);
  }, [backgroundColor, fov]);
  if (!backgroundColor) {
    return null;
  }
  return (
    <div style={{width: '100%', height: '100%'}}>
      <Canvas invalidateFrameloop={invalidateFrameloop} style={{ background: backgroundColor }} camera={{ position: [0, 50, 10], fov }}>
        <Scene />
      </Canvas>
    </div>
  )
}

export default App;
